import React, { useState, useEffect, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import {
  FiMic,
  FiMicOff,
  FiSend,
  FiRefreshCw,
  FiSettings,
  FiVolume2,
  FiVolumeX,
  FiZap,
  FiTrendingUp,
  FiDollarSign,
  FiBarChart2,
  FiGlobe,
  FiUser,
  FiCpu
} from 'react-icons/fi';
import '../styles/saytrix.css';

const Saytrix = ({ user, theme }) => {
  const [isListening, setIsListening] = useState(false);
  const [isProcessing, setIsProcessing] = useState(false);
  const [isSpeaking, setIsSpeaking] = useState(false);
  const [audioEnabled, setAudioEnabled] = useState(true);
  const [messages, setMessages] = useState([
    {
      id: 1,
      type: 'assistant',
      content: 'Hello! I\'m Saytrix, your AI trading assistant. How can I help you today?',
      timestamp: new Date(),
      suggestions: ['/quote RELIANCE', '/news market', '/portfolio']
    }
  ]);
  const [inputText, setInputText] = useState('');
  const messagesEndRef = useRef(null);

  // Sample quick commands
  const quickCommands = [
    { command: '/quote', description: 'Get stock quotes', icon: FiDollarSign },
    { command: '/news', description: 'Latest market news', icon: FiGlobe },
    { command: '/portfolio', description: 'Portfolio analysis', icon: FiBarChart2 },
    { command: '/compare', description: 'Compare stocks', icon: FiTrendingUp }
  ];

  // Sample stock suggestions
  const stockSuggestions = [
    { symbol: 'RELIANCE', price: '2,465', change: '+0.11%' },
    { symbol: 'TCS', price: '3,185', change: '-0.47%' },
    { symbol: 'HDFCBANK', price: '1,650', change: '+3.13%' },
    { symbol: 'INFY', price: '1,596', change: '+1.25%' }
  ];

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  const handleVoiceToggle = () => {
    if (isListening) {
      stopListening();
    } else {
      startListening();
    }
  };

  const startListening = () => {
    setIsListening(true);
    // Simulate voice recognition
    setTimeout(() => {
      setIsListening(false);
      setInputText('What is the current price of Reliance?');
    }, 3000);
  };

  const stopListening = () => {
    setIsListening(false);
  };

  const handleSendMessage = async () => {
    if (!inputText.trim()) return;

    const userMessage = {
      id: Date.now(),
      type: 'user',
      content: inputText,
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputText('');
    setIsProcessing(true);

    // Simulate AI processing
    setTimeout(() => {
      const assistantMessage = {
        id: Date.now() + 1,
        type: 'assistant',
        content: generateResponse(inputText),
        timestamp: new Date(),
        suggestions: generateSuggestions(inputText)
      };

      setMessages(prev => [...prev, assistantMessage]);
      setIsProcessing(false);

      // Simulate text-to-speech
      if (audioEnabled) {
        setIsSpeaking(true);
        setTimeout(() => setIsSpeaking(false), 2000);
      }
    }, 1500);
  };

  const generateResponse = (input) => {
    const lowerInput = input.toLowerCase();
    
    if (lowerInput.includes('reliance') || lowerInput.includes('price')) {
      return 'Reliance Industries (RELIANCE) is currently trading at 2,465.30, up by 2.71 (+0.11%) today. The stock has strong fundamentals with a market cap of 16.7L Cr. Would you like me to show you the technical analysis or recent news?';
    } else if (lowerInput.includes('portfolio')) {
      return 'Your portfolio is performing well! Current value: 2,84,765 (+12.5% overall). Top performers: HDFCBANK (+3.13%), INFY (+1.25%). You have 8 holdings across 4 sectors. Would you like a detailed breakdown?';
    } else if (lowerInput.includes('news') || lowerInput.includes('market')) {
      return 'Here are today\'s top market highlights: Sensex up 501 points, Banking stocks rally on RBI optimism, IT stocks under pressure due to Fed signals. Reliance reports strong Q3 results. Would you like detailed news on any specific sector?';
    } else {
      return 'I understand you\'re asking about trading. I can help you with stock quotes, market analysis, portfolio insights, and trading strategies. Try asking me about specific stocks or use commands like /quote, /news, or /portfolio.';
    }
  };

  const generateSuggestions = (input) => {
    const lowerInput = input.toLowerCase();
    
    if (lowerInput.includes('reliance')) {
      return ['/chart RELIANCE', '/news RELIANCE', '/compare RELIANCE TCS'];
    } else if (lowerInput.includes('portfolio')) {
      return ['/rebalance', '/risk analysis', '/sector allocation'];
    } else {
      return ['/quote NIFTY', '/top gainers', '/market summary'];
    }
  };

  const handleQuickCommand = (command) => {
    setInputText(command + ' ');
  };

  const handleSuggestionClick = (suggestion) => {
    setInputText(suggestion);
  };

  return (
    <div className="saytrix-page">
      <div className="saytrix-container">
        {/* Saytrix Header */}
        <motion.div
          className="saytrix-header"
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
          <div className="header-content">
            <div className="header-left">
              <div className="saytrix-logo">
                <div className="logo-icon">
                  <FiZap className="zap-icon" />
                </div>
                <div className="logo-text">
                  <h1 className="page-title">Saytrix</h1>
                  <p className="page-subtitle">AI Trading Assistant</p>
                </div>
              </div>
            </div>
            <div className="header-actions">
              <button 
                className={`btn-premium btn-ghost ${audioEnabled ? 'active' : ''}`}
                onClick={() => setAudioEnabled(!audioEnabled)}
              >
                {audioEnabled ? <FiVolume2 size={16} /> : <FiVolumeX size={16} />}
                Audio
              </button>
              <button className="btn-premium btn-ghost">
                <FiSettings size={16} />
                Settings
              </button>
            </div>
          </div>
        </motion.div>

        {/* Chat Interface */}
        <div className="chat-layout">
          {/* Quick Commands Sidebar */}
          <motion.div
            className="quick-commands"
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.6, delay: 0.1 }}
          >
            <h3 className="commands-title">Quick Commands</h3>
            <div className="commands-list">
              {quickCommands.map((cmd, index) => {
                const Icon = cmd.icon;
                return (
                  <button
                    key={cmd.command}
                    className="command-btn"
                    onClick={() => handleQuickCommand(cmd.command)}
                  >
                    <Icon size={16} />
                    <div className="command-info">
                      <div className="command-name">{cmd.command}</div>
                      <div className="command-desc">{cmd.description}</div>
                    </div>
                  </button>
                );
              })}
            </div>

            <div className="stock-suggestions">
              <h4 className="suggestions-title">Trending Stocks</h4>
              {stockSuggestions.map(stock => (
                <div key={stock.symbol} className="stock-suggestion">
                  <div className="stock-info">
                    <div className="stock-symbol">{stock.symbol}</div>
                    <div className="stock-price">{stock.price}</div>
                  </div>
                  <div className={`stock-change ${stock.change.startsWith('+') ? 'positive' : 'negative'}`}>
                    {stock.change}
                  </div>
                </div>
              ))}
            </div>
          </motion.div>

          {/* Chat Messages */}
          <motion.div
            className="chat-main"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.2 }}
          >
            <div className="messages-container">
              <AnimatePresence>
                {messages.map((message) => (
                  <motion.div
                    key={message.id}
                    className={`message ${message.type}`}
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, y: -20 }}
                    transition={{ duration: 0.3 }}
                  >
                    <div className="message-avatar">
                      {message.type === 'user' ? (
                        <FiUser size={20} />
                      ) : (
                        <FiCpu size={20} />
                      )}
                    </div>
                    <div className="message-content">
                      <div className="message-text">{message.content}</div>
                      {message.suggestions && (
                        <div className="message-suggestions">
                          {message.suggestions.map((suggestion, index) => (
                            <button
                              key={index}
                              className="suggestion-btn"
                              onClick={() => handleSuggestionClick(suggestion)}
                            >
                              {suggestion}
                            </button>
                          ))}
                        </div>
                      )}
                      <div className="message-time">
                        {message.timestamp.toLocaleTimeString('en-IN', { 
                          hour: '2-digit', 
                          minute: '2-digit' 
                        })}
                      </div>
                    </div>
                  </motion.div>
                ))}
              </AnimatePresence>

              {isProcessing && (
                <motion.div
                  className="message assistant processing"
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                >
                  <div className="message-avatar">
                    <FiCpu size={20} />
                  </div>
                  <div className="message-content">
                    <div className="typing-indicator">
                      <span></span>
                      <span></span>
                      <span></span>
                    </div>
                  </div>
                </motion.div>
              )}

              <div ref={messagesEndRef} />
            </div>

            {/* Input Area */}
            <div className="input-area">
              <div className="input-container">
                <button
                  className={`voice-btn ${isListening ? 'listening' : ''}`}
                  onClick={handleVoiceToggle}
                >
                  {isListening ? (
                    <motion.div
                      animate={{ scale: [1, 1.2, 1] }}
                      transition={{ duration: 1, repeat: Infinity }}
                    >
                      <FiMic size={20} />
                    </motion.div>
                  ) : (
                    <FiMicOff size={20} />
                  )}
                </button>
                
                <input
                  type="text"
                  className="message-input"
                  placeholder="Ask me about stocks, portfolio, or market trends..."
                  value={inputText}
                  onChange={(e) => setInputText(e.target.value)}
                  onKeyPress={(e) => e.key === 'Enter' && handleSendMessage()}
                />
                
                <button
                  className="send-btn"
                  onClick={handleSendMessage}
                  disabled={!inputText.trim() || isProcessing}
                >
                  {isProcessing ? (
                    <FiRefreshCw size={20} className="animate-spin" />
                  ) : (
                    <FiSend size={20} />
                  )}
                </button>
              </div>

              {isListening && (
                <motion.div
                  className="listening-indicator"
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                >
                  <div className="listening-animation">
                    <div className="wave"></div>
                    <div className="wave"></div>
                    <div className="wave"></div>
                  </div>
                  <span>Listening... Speak now</span>
                </motion.div>
              )}

              {isSpeaking && (
                <motion.div
                  className="speaking-indicator"
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                >
                  <FiVolume2 size={16} />
                  <span>Saytrix is speaking...</span>
                </motion.div>
              )}
            </div>
          </motion.div>
        </div>
      </div>
    </div>
  );
};

export default Saytrix;
